<template>
  <div class="emerging-job-chart">
    <div class="chart-title">
      <span class="title-text"></span>
      <div class="title-decoration"></div>
    </div>
    
    <div ref="chartContainer" class="chart-container" v-show="viewMode === 'chart'"></div>
    
    <!-- ËÅå‰ΩçÂç°ÁâáÂÆπÂô® -->
    <div class="job-cards-container" v-show="viewMode === 'cards'">
      <div 
        v-for="(job, index) in jobData" 
        :key="index"
        class="job-card"
        :class="{ 'active': selectedJobIndex === index }"
        @click="selectJob(index)"
      >
        <div class="job-header">
          <div class="job-title">{{ job.job_title }}</div>
          <div class="job-rank">
            <span class="rank-number">#{{ index + 1 }}</span>
            <span class="trend-badge" :class="job.trendClass">{{ job.trendText }}</span>
          </div>
        </div>
        
        <div class="metric-container">
          <div class="metric-row">
            <div class="metric salary">
              <div class="metric-value">{{ formatSalary(job.avg_salary) }}</div>
              <div class="metric-label">Âπ≥ÂùáËñ™ËµÑ</div>
              <div class="metric-icon">üí∞</div>
            </div>
            <div class="metric count">
              <div class="metric-value">{{ formatNumber(job.count) }}</div>
              <div class="metric-label">ÊãõËÅòÊï∞Èáè</div>
              <div class="metric-icon">üë•</div>
            </div>
          </div>
          
          <div class="score-bar">
            <div class="score-label">ÁªºÂêàËØÑÂàÜ</div>
            <div class="score-track">
              <div class="score-value" :style="{ width: `${job.score}%` }">
                {{ job.score.toFixed(1) }}
              </div>
            </div>
          </div>
          
          <div class="job-details">
            <div class="detail-item">
              <span class="detail-label">Â∞±‰∏öÂâçÊôØ:</span>
              <span class="detail-rating">
                <span class="star" v-for="n in 5" :key="n" :class="{ 'filled': n <= job.prospectRating }">‚òÖ</span>
              </span>
            </div>
            <!-- <div class="detail-item">
              <span class="detail-label">Ë°å‰∏öÂàÜÂ∏É:</span>
              <span class="detail-badges">
                <span class="industry-badge" v-for="(ind, i) in job.industries" :key="i">{{ ind }}</span>
              </span>
            </div> -->
          </div>
        </div>
      </div>
    </div>
    
    <!-- ËßÜÂõæÂàáÊç¢ÊåâÈíÆ -->
    <div class="view-toggle">
      <button 
        class="toggle-btn" 
        :class="{ 'active': viewMode === 'chart' }"
        @click="setViewMode('chart')"
      >
        <i class="toggle-icon">üìä</i>ÂõæË°®ËßÜÂõæ
      </button>
      <button 
        class="toggle-btn" 
        :class="{ 'active': viewMode === 'cards' }"
        @click="setViewMode('cards')"
      >
        <i class="toggle-icon">üóÇÔ∏è</i>Âç°ÁâáËßÜÂõæ
      </button>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue'
import * as echarts from 'echarts'

const props = defineProps({ 
  data: {
    type: Array,
    default: () => []
  }
})

const chartContainer = ref(null)
const viewMode = ref('chart')
const selectedJobIndex = ref(0)
let chartInstance = null

// ÁîüÊàêÈöèÊú∫Ë°å‰∏ö
const generateIndustries = (job) => {
  const allIndustries = ['‰∫íËÅîÁΩë', 'ÈáëËûç', 'ITÊúçÂä°', 'ÊïôËÇ≤', 'ÂåªÁñó', 'Âà∂ÈÄ†‰∏ö', 'Èõ∂ÂîÆ', 'Âí®ËØ¢', 'ÈÄö‰ø°', 'ÊàøÂú∞‰∫ß']
  const count = 1 + Math.floor(Math.random() * 3) // 1-3‰∏™Ë°å‰∏ö
  const industries = []
  
  // Ê†πÊçÆËÅå‰ΩçÂêçÁß∞ÈÄâÊã©Áõ∏ÂÖ≥Ë°å‰∏ö
  if (job.job_title.includes('ÂºÄÂèë') || job.job_title.includes('Â∑•Á®ãÂ∏à') || job.job_title.includes('Á®ãÂ∫è')) {
    industries.push('‰∫íËÅîÁΩë', 'ITÊúçÂä°')
  } else if (job.job_title.includes('ÈîÄÂîÆ') || job.job_title.includes('Ëê•ÈîÄ')) {
    industries.push('Èõ∂ÂîÆ', 'Âí®ËØ¢')
  } else if (job.job_title.includes('ÈáëËûç') || job.job_title.includes('ÂàÜÊûê')) {
    industries.push('ÈáëËûç', 'Âí®ËØ¢')
  } else if (job.job_title.includes('ÊïôËÇ≤') || job.job_title.includes('ËÄÅÂ∏à')) {
    industries.push('ÊïôËÇ≤')
  } else if (job.job_title.includes('Âåª') || job.job_title.includes('Êä§')) {
    industries.push('ÂåªÁñó')
  } else {
    // ÈöèÊú∫ÈÄâÊã©Ë°å‰∏ö
    while (industries.length < count) {
      const ind = allIndustries[Math.floor(Math.random() * allIndustries.length)]
      if (!industries.includes(ind)) {
        industries.push(ind)
      }
    }
  }
  
  return industries
}

// Â¢ûÂº∫ËÅå‰ΩçÊï∞ÊçÆ
const jobData = computed(() => {
  if (!props.data || !props.data.length) return []
  
  // Ëé∑ÂèñÊúÄÈ´òËñ™ËµÑÂíåÊãõËÅòÊï∞ÈáèÔºåÁî®‰∫éÂΩí‰∏ÄÂåñ
  const maxSalary = Math.max(...props.data.map(job => {
    return typeof job.avg_salary === 'number' ? 
      job.avg_salary : parseFloat(job.avg_salary) || 0
  }))
  
  const maxCount = Math.max(...props.data.map(job => {
    return typeof job.count === 'number' ? 
      job.count : parseFloat(job.count) || 0
  }))
  
  return props.data.map(job => {
    // Á°Æ‰øùÊï∞ÊçÆÁ±ªÂûãÊ≠£Á°Æ
    const avgSalary = typeof job.avg_salary === 'number' ? 
      job.avg_salary : parseFloat(job.avg_salary) || 0
      
    const count = typeof job.count === 'number' ? 
      job.count : parseFloat(job.count) || 0
      
    // ÂΩí‰∏ÄÂåñÂ§ÑÁêÜ
    const normalizedSalary = avgSalary / maxSalary
    const normalizedCount = count / maxCount
    
    // ËÆ°ÁÆóÁªºÂêàËØÑÂàÜ (Ëñ™ËµÑÂç†70%, Êï∞ÈáèÂç†30%)
    const score = (normalizedSalary * 0.7 + normalizedCount * 0.3) * 100
    
    // Ê†πÊçÆËØÑÂàÜÁ°ÆÂÆöË∂ãÂäø
    let trend, trendText, trendClass
    if (score >= 75) {
      trend = 'rapid-rise'
      trendText = 'Âø´ÈÄüÂ¢ûÈïø'
      trendClass = 'trend-rapid-rise'
    } else if (score >= 50) {
      trend = 'rise'
      trendText = 'Á®≥Ê≠•‰∏äÂçá'
      trendClass = 'trend-rise'
    } else if (score >= 25) {
      trend = 'stable'
      trendText = 'ÈúÄÊ±ÇÁ®≥ÂÆö'
      trendClass = 'trend-stable'
    } else {
      trend = 'decline'
      trendText = 'ÈúÄÊ±Ç‰∏ãÈôç'
      trendClass = 'trend-decline'
    }
    
    // Â∞±‰∏öÂâçÊôØËØÑÂàÜ (1-5Êòü)
    const prospectRating = Math.ceil((score / 100) * 5)
    
    // ÁîüÊàêÁõ∏ÂÖ≥Ë°å‰∏ö
    const industries = generateIndustries(job)
    
    return {
      job_title: job.job_title || 'Êú™Áü•ËÅå‰Ωç',
      avg_salary: avgSalary,
      count: count,
      score,
      trend,
      trendText,
      trendClass,
      prospectRating,
      industries
    }
  })
})

// Ê†ºÂºèÂåñËñ™ËµÑÊòæÁ§∫
const formatSalary = (salary) => {
  if (typeof salary !== 'number') {
    salary = parseFloat(salary) || 0
  }
  return salary.toFixed(1) + 'K'
}

// Ê†ºÂºèÂåñÊï∞Â≠óÊòæÁ§∫
const formatNumber = (num) => {
  if (typeof num !== 'number') {
    num = parseFloat(num) || 0
  }
  
  if (num >= 10000) {
    return (num / 10000).toFixed(1) + '‰∏á'
  }
  return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
}

const initChart = () => {
  if (!chartContainer.value || jobData.value.length === 0) return
  
  if (chartInstance) {
    chartInstance.dispose()
  }
  
  chartInstance = echarts.init(chartContainer.value)
  
  // Êï∞ÊçÆÂáÜÂ§á
  const jobTitles = jobData.value.map(job => {
    if (job.job_title.length > 8) {
      return job.job_title.substring(0, 8) + '...'
    }
    return job.job_title
  })
  
  // ÂàõÂª∫‰∏§‰∏™Áª¥Â∫¶Êï∞ÊçÆ: Ëñ™ËµÑÂíåÊï∞Èáè
  const salaryData = jobData.value.map(job => job.avg_salary)
  const countData = jobData.value.map(job => job.count)
  const scoreData = jobData.value.map(job => job.score)
  
  const option = {
    backgroundColor: 'transparent',
    title: {
      show: false
    },
    tooltip: {
      trigger: 'axis',
      axisPointer: { type: 'shadow' },
      formatter: (params) => {
        const index = params[0].dataIndex
        const job = jobData.value[index]
        if (!job) return ''
        
        return `
          <div style="font-family: 'Microsoft YaHei', sans-serif;">
            <div style="font-weight:bold;color:#6EDDF1;margin-bottom:8px;font-size:16px;">${job.job_title}</div>
            <div style="display:flex;justify-content:space-between;margin-bottom:5px;">
              <span style="color:#eee;min-width:70px;">Âπ≥ÂùáËñ™ËµÑ:</span>
              <span style="color:#FFD700;font-weight:bold;">${formatSalary(job.avg_salary)}</span>
            </div>
            <div style="display:flex;justify-content:space-between;margin-bottom:5px;">
              <span style="color:#eee;min-width:70px;">ÊãõËÅòÊï∞Èáè:</span>
              <span style="color:#fff;font-weight:bold;">${formatNumber(job.count)}</span>
            </div>
            <div style="display:flex;justify-content:space-between;margin-bottom:5px;">
              <span style="color:#eee;min-width:70px;">ÁªºÂêàËØÑÂàÜ:</span>
              <span style="color:#FF7E45;font-weight:bold;">${job.score.toFixed(1)}</span>
            </div>
            <div style="display:flex;justify-content:space-between;margin-bottom:5px;">
              <span style="color:#eee;min-width:70px;">ÂèëÂ±ïË∂ãÂäø:</span>
              <span style="color:#fff;">${job.trendText}</span>
            </div>
          </div>
        `
      },
      backgroundColor: 'rgba(13, 36, 81, 0.9)',
      borderColor: 'rgba(109, 198, 255, 0.4)',
      borderWidth: 1,
      padding: 10,
      textStyle: {
        color: '#fff'
      }
    },
    grid: {
      top: 60,
      bottom: 40,
      left: 50,
      right: 50,
      containLabel: true
    },
    legend: {
      data: ['Âπ≥ÂùáËñ™ËµÑ', 'ÊãõËÅòÊï∞Èáè', 'ÁªºÂêàËØÑÂàÜ'],
      textStyle: { color: '#ddd' },
      top: 20,
      itemGap: 30
    },
    xAxis: [
      {
        type: 'category',
        data: jobTitles,
        axisLabel: {
          color: '#fff',
          rotate: 45,
          fontSize: 12,
          interval: 0,
          hideOverlap: true
        },
        axisLine: {
          lineStyle: {
            color: 'rgba(255, 255, 255, 0.3)'
          }
        },
        axisTick: {
          alignWithLabel: true,
          lineStyle: {
            color: 'rgba(255, 255, 255, 0.3)'
          }
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        name: 'Âπ≥ÂùáËñ™ËµÑ (K)',
        position: 'left',
        nameTextStyle: {
          color: '#00c0ff',
          fontSize: 12,
          padding: [0, 0, 0, -20]
        },
        axisLabel: {
          color: '#ddd',
          formatter: '{value}K'
        },
        axisLine: {
          show: true,
          lineStyle: {
            color: '#00c0ff'
          }
        },
        splitLine: {
          lineStyle: {
            color: 'rgba(255, 255, 255, 0.1)'
          }
        }
      },
      {
        type: 'value',
        name: 'ÊãõËÅòÊï∞Èáè',
        position: 'right',
        nameTextStyle: {
          color: '#ff7a45',
          fontSize: 12,
          padding: [0, -20, 0, 0]
        },
        axisLabel: {
          color: '#ddd',
          formatter: (value) => {
            if (value >= 10000) {
              return (value / 10000).toFixed(1) + '‰∏á'
            }
            return value
          }
        },
        axisLine: {
          show: true,
          lineStyle: {
            color: '#ff7a45'
          }
        },
        splitLine: {
          show: false
        }
      }
    ],
    series: [
      {
        name: 'Âπ≥ÂùáËñ™ËµÑ',
        type: 'bar',
        data: salaryData,
        itemStyle: {
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            { offset: 0, color: '#00c0ff' },
            { offset: 1, color: '#0082af' }
          ]),
          borderRadius: [6, 6, 0, 0]
        },
        barWidth: '25%',
        barGap: '30%',
        label: {
          show: true,
          position: 'top',
          formatter: '{c}K',
          color: '#fff',
          fontSize: 12
        },
        emphasis: {
          itemStyle: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              { offset: 0, color: '#33d6ff' },
              { offset: 1, color: '#00a3d9' }
            ])
          }
        }
      },
      {
        name: 'ÊãõËÅòÊï∞Èáè',
        type: 'bar',
        yAxisIndex: 1,
        data: countData,
        itemStyle: {
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            { offset: 0, color: '#ff7a45' },
            { offset: 1, color: '#cc4400' }
          ]),
          borderRadius: [6, 6, 0, 0]
        },
        barWidth: '25%',
        label: {
          show: true,
          position: 'top',
          color: '#fff',
          fontSize: 12,
          formatter: (params) => {
            const value = params.value
            if (value >= 10000) {
              return (value / 10000).toFixed(1) + '‰∏á'
            }
            return value
          }
        },
        emphasis: {
          itemStyle: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              { offset: 0, color: '#ff956c' },
              { offset: 1, color: '#ee5522' }
            ])
          }
        }
      },
      {
        name: 'ÁªºÂêàËØÑÂàÜ',
        type: 'line',
        smooth: true,
        symbol: 'circle',
        symbolSize: 10,
        data: scoreData,
        z: 3,
        lineStyle: {
          width: 4,
          color: {
            type: 'linear',
            x: 0,
            y: 0,
            x2: 1,
            y2: 0,
            colorStops: [
              { offset: 0, color: '#faad14' },
              { offset: 1, color: '#ff6e76' }
            ]
          },
          shadowColor: 'rgba(250, 173, 20, 0.3)',
          shadowBlur: 10
        },
        itemStyle: {
          color: '#faad14',
          borderWidth: 3,
          borderColor: '#fff'
        },
        label: {
          show: true,
          position: 'top',
          formatter: (params) => {
            return params.value.toFixed(1)
          },
          color: '#faad14',
          fontSize: 12,
          fontWeight: 'bold'
        },
        emphasis: {
          scale: true
        }
      }
    ],
    // Ê∑ªÂä†Âä®ÁîªÊïàÊûú
    animationDuration: 2000,
    animationEasing: 'elasticOut',
    animationDelayUpdate: (idx) => idx * 200
  }

  chartInstance.setOption(option)
  
  // ÁÇπÂáª‰∫ã‰ª∂
  chartInstance.on('click', (params) => {
    if (params.componentType === 'series') {
      selectedJobIndex.value = params.dataIndex
      setViewMode('cards')
    }
  })
}

// ËÆæÁΩÆËßÜÂõæÊ®°Âºè
const setViewMode = (mode) => {
  viewMode.value = mode
  
  // Â¶ÇÊûúÂàáÊç¢Âà∞ÂõæË°®Ê®°ÂºèÔºåÁ°Æ‰øùÂõæË°®Ê∏≤ÊüìÊ≠£Á°Æ
  if (mode === 'chart' && chartContainer.value) {
    setTimeout(() => {
      initChart()
    }, 50)
  }
}

// ÈÄâÊã©ËÅå‰Ωç
const selectJob = (index) => {
  selectedJobIndex.value = index
}

onMounted(() => {
  if (jobData.value && jobData.value.length > 0) {
    initChart()
  }
})

watch(() => props.data, (newVal) => {
  if (newVal && newVal.length > 0) {
    initChart()
  }
}, { deep: true })

// ÂìçÂ∫îÁ™óÂè£Â§ßÂ∞èÂèòÂåñ
window.addEventListener('resize', () => {
  if (chartInstance && viewMode.value === 'chart') {
    chartInstance.resize()
  }
})
</script>

<style lang="less" scoped>
.emerging-job-chart {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 15px;
  box-sizing: border-box;
}

.chart-title {
  text-align: center;
  margin-bottom: 10px;
  position: relative;
}

.title-text {
  color: #6EDDF1;
  font-size: 16px;
  font-weight: bold;
  text-shadow: 0 0 10px rgba(110, 221, 241, 0.3);
}

.title-decoration {
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 2px;
  background: linear-gradient(90deg, transparent, #6EDDF1, transparent);
}

.chart-container {
  width: 100%;
  flex: 1;
  transition: opacity 0.3s;
  position: relative;
}

.chart-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(ellipse at center, rgba(110, 221, 241, 0.03) 0%, transparent 70%);
  pointer-events: none;
}

.job-cards-container {
  position: absolute;
  top: 50px;
  left: 15px;
  right: 15px;
  bottom: 50px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 15px;
  transition: opacity 0.3s;
  
  &::-webkit-scrollbar {
    width: 5px;
  }
  
  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 3px;
  }
  
  &::-webkit-scrollbar-thumb {
    background: rgba(110, 221, 241, 0.4);
    border-radius: 3px;
  }
}

.job-card {
  background: rgba(21, 40, 90, 0.7);
  border: 1px solid rgba(109, 198, 255, 0.3);
  border-radius: 12px;
  padding: 15px;
  cursor: pointer;
  transition: all 0.3s;
  animation: fadeIn 0.5s ease-out;
  
  &:hover {
    background: rgba(32, 56, 117, 0.8);
    border-color: rgba(109, 198, 255, 0.5);
    transform: translateY(-3px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
  }
  
  &.active {
    background: rgba(48, 78, 138, 0.8);
    border-color: rgba(110, 221, 241, 0.7);
    box-shadow: 0 0 20px rgba(110, 221, 241, 0.3);
  }
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.job-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 15px;
}

.job-title {
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  flex: 1;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.2);
  line-height: 1.3;
}

.job-rank {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 5px;
}

.rank-number {
  background: rgba(110, 221, 241, 0.2);
  color: #6EDDF1;
  font-weight: bold;
  font-size: 14px;
  padding: 3px 8px;
  border-radius: 12px;
}

.trend-badge {
  font-size: 12px;
  padding: 3px 8px;
  border-radius: 12px;
  font-weight: 500;
  
  &.trend-rapid-rise {
    background: rgba(0, 255, 0, 0.1);
    color: #00ff00;
  }
  
  &.trend-rise {
    background: rgba(0, 200, 83, 0.1);
    color: #00c853;
  }
  
  &.trend-stable {
    background: rgba(255, 193, 7, 0.1);
    color: #ffc107;
  }
  
  &.trend-decline {
    background: rgba(255, 87, 34, 0.1);
    color: #ff5722;
  }
}

.metric-container {
  background: rgba(13, 36, 81, 0.5);
  border-radius: 8px;
  padding: 12px;
}

.metric-row {
  display: flex;
  gap: 15px;
  margin-bottom: 15px;
}

.metric {
  flex: 1;
  padding: 10px;
  border-radius: 6px;
  position: relative;
  text-align: center;
  
  &.salary {
    background: linear-gradient(145deg, rgba(0, 192, 255, 0.1), rgba(0, 128, 175, 0.2));
    
    .metric-value {
      color: #00c0ff;
    }
  }
  
  &.count {
    background: linear-gradient(145deg, rgba(255, 122, 69, 0.1), rgba(204, 68, 0, 0.2));
    
    .metric-value {
      color: #ff7a45;
    }
  }
  
  .metric-icon {
    position: absolute;
    top: 8px;
    right: 8px;
    font-size: 16px;
